.Control {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin: 5px 0px;
}

.Control button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0px 5px;
    width: 80px;
    border: 1px solid #aa6817;
    cursor: pointer;
    outline: none;
}


.Control button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.Control button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.Control .Less {  
    background-color: #D39952;
    color: white;
}

.Control .More {
    background-color: #8F5E1E;
    color: white;
}

.Control .Less:hover, .Control .Less:active {  
    background-color: #DAA972;
    color: white;
}

.Control .More:hover,.Control .More:active {
    background-color: #99703F;
    color: white;
}


